        <div class>
          <div class="homecontent-pie2-wrapper">
            <div class="homecontent-pie2-wrapper-left">
              <KaFen></KaFen>
              <div class="echarts-title-wrapper">
                <div class="echarts-title-wrapper-border"></div>
                <div class="echarts-title-wrapper-text">卡分层</div>
                <div class="echarts-title-wrapper-border"></div>
              </div>
            </div>
            <div class="homecontent-pie2-wrapper-right">
              <KeHuZiChan></KeHuZiChan>
              <div class="echarts-title-wrapper">
                <div class="echarts-title-wrapper-border"></div>
                <div class="echarts-title-wrapper-text">客户资产分布</div>
                <div class="echarts-title-wrapper-border"></div>
              </div>
            </div>
          </div>
          <div class="homecontent-pie3-wrapper">
            <div class="homecontent-pie3-wrapper-left">
              <KeHuFenCeng></KeHuFenCeng>
              <div class="echarts-title-wrapper">
                <div class="echarts-title-wrapper-border"></div>
                <div class="echarts-title-wrapper-text">客户分层</div>
                <div class="echarts-title-wrapper-border"></div>
              </div>
            </div>
          </div>
          <p style="clear:both"></p>
        </div>
      </div>

      <div>
        <div class="homecontent-line1-wrapper">
          <Nian></Nian>
        </div>
      </div>
      <div>
        <div class="homecontent-line2-wrapper">
