</template>
<script>
import XingBie from "./XingBie";
import NianLing from "./NianLing";
import FengXian from "./FengXian";
import KaFen from "./KaFen";
import KeHuZiChan from "./KeHuZiChan";
import KeHuFenCeng from "./KeHuFenCeng";
import Yve from "./Yve";
import Nian from "./Nian";
import { mapState } from "vuex";

let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/bar"); // 引入柱状图组件
require("echarts/lib/chart/pie"); // 引入饼状图组件
require("echarts/map/js/china.js"); // 引入中国地图组件
// 引入提示框和title组件
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
require("echarts/theme/macarons"); //引入主题
require("echarts/theme/shine"); //引入主题
export default {
  name: "HomeContent",
  components: {
    XingBie,
    NianLing,
    FengXian,
    KaFen,
    KeHuZiChan,
    KeHuFenCeng,
    Yve,
    Nian
  },
  data() {
    return {
      // cardList: [
      //   { id: 1, name: "客户总数", value: "123,123,123" },
      //   { id: 2, name: "非0客户数", value: "123,123,123" },
      //   { id: 3, name: "金融资产", value: "123,123,123" },
      //   { id: 4, name: "当年基础及以上客户净增", value: "123,123,123" },
      //   { id: 5, name: "当年中高端客户净增", value: "123,123,123" },
      //   { id: 6, name: "当年四绑客户净增", value: "123,123,123" },
      //   { id: 7, name: "当年三方代理基础及以上客户净增", value: "123,123,123" },
      //   { id: 8, name: "网均总客户数", value: "123,123,123" },
      //   { id: 9, name: "网均非0客户数", value: "123,123,123" },
      //   { id: 10, name: "网均金融资产", value: "123,123,123" },
      //   { id: 11, name: "网均当年基础及以上客户净增", value: "123,123,123" },
      //   { id: 12, name: "网均当年中高端客户净增", value: "123,123,123" },
      //   { id: 13, name: "网均当年四绑客户净增", value: "123,123,123" },
      //   {
      //     id: 14,
      //     name: "网均当年三方代理基础及以上客户净增",
      //     value: "123,123,123"
      //   }
      // ]
      mapIndex: 0,
      curCity: "",
      curFenHang:""
    };
  },
  methods: {
    toPercent(val, flag) {
      //数组转小数
      if (flag) {
        return (Math.round(val * 10000) / 100).toFixed(2);
      } else {
        return (Math.round(val * 10000) / 100).toFixed(2) + "%";
      }
    },
    // 处理世界地图
    drawChinaMap(id, ChinaMapData) {
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));
      //地图初始化地图
      var chinaMap = echarts.init(document.getElementById(id));

      var count = 0;
      let _this = this;
      var data = [];
      console.log(ChinaMapData);
      console.log(Object.prototype.toString.call([{}]));
      if (Object.prototype.toString.call(ChinaMapData) !== "[object Array]") {
        // 支行
        ChinaMapData = [ChinaMapData];
        ChinaMapData.forEach(element => {
          data.push({
            orgName: element.orgName,
            name: element.provinceName,
            value: element.branCustomerCount,
            quanHangZongKeHuShu: element.headBankCustomerCount,
            feiLengKeHuShu: element.branNoZeroCustomer,
            quanHangFeiLingKeHuShu: element.headBankNoZeroCustomer,
            feiLengZhanBi: element.branNoZeroCustomerPercent,
            quanHangFeiLingZhanBi: element.headBankNoZeroCustomerPercent,
            AUMGuiMu: element.branAUMAsset,
            AUMZhanQuanHang: element.branAUMAssetsPercent
          });
        });
      } else {
        // 总行
        ChinaMapData.forEach(element => {
          data.push({
            orgName: element.org_name, // 银行名
            name: element.provinceName, // 省名
            value: element.amt, // value
            city: element.cityName, // 城市名
            org_id: element.org_id
          });
        });
      }

      console.log(data);
      let option = {
        visualMap: {
          min: 0,
          max: 10000000,
          right: "50px",
          top: "bottom",
          // color:"#fff",
          text: ["高", "低"],
          calculable: false,
          orient: "horizontal",
          textStyle: {
            color: "#fff"
          },
          inRange: {
            color: ["rgb(0,207,255)", "rgb(0,72,141)"],
            symbolSize: [1, 1]
          }
        },
        tooltip: {
          padding: 10,
          enterable: true,
          transitionDuration: 1,
          textStyle: {
            color: "#fff",
            decoration: "none"
          },
          formatter: function(params) {
            let data = params.data;
            let str = `
                ${data.orgName} </br>
                总客户数 : ${data.value},全行 : ${
              data.quanHangZongKeHuShu
            } </br>
                非0客户 : ${data.feiLengKeHuShu},全行 : ${
              data.quanHangFeiLingKeHuShu
            } </br>
                非0占比 : ${_this.toPercent(
                  data.feiLengZhanBi
                )},全行 : ${_this.toPercent(data.quanHangFeiLingZhanBi)} </br>
                AUM规模 : ${data.AUMGuiMu},占全行 : ${_this.toPercent(
              data.AUMZhanQuanHang
            )}
            `;
            return str;
          }
          // position: function (point, params, dom, rect, size) {
          //   return [point[0], point[1]];
          // },
        },
        series: [
          {
            name: "",
            type: "map",
            mapType: "china",
            roam: true,
            zoom: 1.2, // 地图大小
            itemStyle: {
              normal: {
                areaColor: "rgb(0,207,255)",
                borderColor: "rgb(174,255,253)"
              },
              emphasis: {
                areaColor: "rgb(255,213,116)" // 选中颜色
              }
            },
            // itemStyle: {
            //   normal: {
            //     label: {
            //       show: false
            //     }
            //   },
            //   emphasis: {
            //     label: {
            //       show: true
            //     }
            //   }
            // },
            data: data
          }
        ]
      };
      var self = this;
      var timeTicket = null;
      var dataLength = option.series[0].data.length;
      timeTicket && clearInterval(timeTicket);
      timeTicket = setInterval(function() {
        chinaMap.dispatchAction({
          type: "downplay",
          seriesIndex: 0
        });
        chinaMap.dispatchAction({
          type: "highlight",
          seriesIndex: 0,
          dataIndex: count % dataLength
        });
        chinaMap.dispatchAction({
          type: "showTip",
          seriesIndex: 0,
          dataIndex: count % dataLength
        });
        self.currentCity = option.series[0].data[count % dataLength];
        self.$emit("currentCityFn", self.currentCity);

        count++;
      }, 3000);

      chinaMap.setOption(option);
      chinaMap.on("click", function(e) {
        let data = e.data;
        let org_id;
        if (self.curCity !== data.name) {
          self.curCity = data.name;
          self.mapIndex = 0;
        }
        let curCity = data.name;
        if (data.org_id.indexOf(",") !== -1) {
          let org_id_arr = data.org_id.split(",");
          let org_name_arr = data.orgName.split(",");
          org_id = org_id_arr[self.mapIndex];
          self.curFenHang = `
          当前为 ${org_name_arr[self.mapIndex]} 分行,再次点击${data.name},切换至${data.name}其他分行
          `
          self.mapIndex++;
          if (self.mapIndex > org_id_arr.length - 1) {
            self.mapIndex = 0;
          }
        } else {
          self.curFenHang = ''
          org_id = e.data.org_id;
        }
        self.$store.commit("set_org_id", org_id);
      });
    }
  },
  computed: {
    ...mapState({
      cardList: state => {
        console.log(state.TopBaseData);
        return state.TopBaseData;
      },
      MapAllData: state => {
        console.log(state.MapAllData);
        return state.MapAllData;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    MapAllData: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawChinaMap("china-map", this.MapAllData);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    let datatest = [
      {
        name: "上海",
        value: "1391939200"
      },
      {
        name: "杭州",
        value: "941926000"
      },
      {
        name: "北京",
        value: "79617900"
      },
      {
        name: "湖南",
        value: "79617900"
      },
      {
        name: "陕西",
        value: "79617900"
      },
      {
        name: "吉林",
        value: "79617900"
      },
      {
        name: "青海",
        value: "79617900"
      },
      {
        name: "深圳",
        value: "6557200"
      },
      {
        name: "广州",
        value: "340700"
      }
    ];
    self.$nextTick(function() {
      console.log(self.MapAllData);
      this.drawChinaMap("china-map", self.MapAllData);
    });
  }
};
</script>

