<template>
  <div id="echarts-kafen">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    drawLine(id, pieData) {
      let self = this;
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      var value = [];
      var KeHuZiChanData = [];
      console.log(pieData);
      pieData.forEach((element, index) => {
        KeHuZiChanData.push([]);
        element.retailCustomerBaseDataList.forEach(element1 => {
          KeHuZiChanData[index].push({
            name: element1.dims4,
            value: element1.amt,
            FenHangValue: element1.branPercent,
            QuanHangValue: element1.headPercent
          });
        });
        value.push({
          value: element.amt,
          name: element.dims4,
          KeHuZiChanData: KeHuZiChanData[index]
        });
      });
      console.log(value);
      self.$store.commit("Set_KeHuZiChanData", value[0]);
      // value = [
      //         {
      //           value: 10,
      //           name: "Ⅰ类卡"
      //         },
      //         {
      //           value: 12,
      //           name: "Ⅱ类卡"
      //         },
      //         {
      //           value: 15,
      //           name: "Ⅲ类卡"
      //         }
      //       ]
      // 绘制图表
      let option = {
        // backgroundColor:"#0B1837",
        color: [
          "#EAEA26",
          "#906BF9",
          "#FE5656",
          "#01E17E",
          "#3DD1F9",
          "#FFAD05"
        ],
        grid: {
          left: 0,
          top: 0,
          bottom: 0,
          right: 0,
          containLabel: true
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },

        series: [
          {
            stack: "a",
            type: "pie",
            radius: ["0%", "55%"],
            // roseType: "area", // 玫瑰图
            zlevel: 10,
            // label: {
            //     normal: {
            //         show: true,
            //         formatter: "{c}",
            //         textStyle: {
            //             fontSize: 12,
            //         },
            //         position: 'outside'
            //     },
            //     emphasis: {
            //         show: true
            //     }
            // },
            label: {
              normal: {
                formatter: "{per|{b}} ",
                align: "center",
                position: "inside", //标签的位置
                borderWidth: 0,
                borderRadius: 4,
                // padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#fff",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#fff",
                  fontSize: 13
                }
              }
            },
            itemStyle: {
              color: "pink",
              borderColor: "#FFF",
              normal: {
                borderWidth: "5",
                borderColor: "#FFF"
              }
            },
            labelLine: {
              normal: {
                show: true,
                length: 10,
                length2: 15
              },
              emphasis: {
                show: false
              }
            },
            data: value
          }
        ]
      };

      myChart.setOption(option);
      myChart.on("click", function(e) {
        console.log(e);
        self.$store.commit("Set_KeHuZiChanData", e.data);
      });
    }
  },
  computed: {
    ...mapState({
      KaFenDate: state => {
        return state.CardSlice;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    KaFenDate: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-kafen", this.KaFenDate);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-kafen", this.KaFenDate);
    });
  }
};
</script>
<style>
#echarts-kafen {
  width: 100%;
  height: 100%;
}
</style>