<template>
  <div id="echarts-nian">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    changeTime(str) {
      let newStr = "";
      newStr = str.substring(0, 4) + "/" + str.substring(5, 7);
      return newStr;
    },
        toPercent(val, flag) {
      //数组转小数
      if (flag) {
        return (Math.round(val * 10000) / 100).toFixed(2);
      } else {
        return (Math.round(val * 10000) / 100).toFixed(2) + "%";
      }
    },
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      var myColor = ["red", "green", "blue", "#325382"];
      var value = pieData;
      var name = [];
      var xData = [];
      var yData = [];
      var monthDatLine = [];
      var yearDatLine = [];
      var incrThanYearBegin = [];
      var incrThanLastMonthEnd = [];
      // console.log(pieData);
      pieData.forEach((element, index) => {
        xData[index] = [];
        yData[index] = [];
        element.retailCustomerBaseDataList.forEach(element1 => {
          xData[index].push(this.changeTime(element1.data_date));
          yData[index].push({
            value: element1.amt,
            XD:this.changeTime(element1.data_date),
            MD:element.monthDatLine,
            YD:element.yearDatLine,
            YB:element.incrThanYearBegin,
            ME:element.incrThanLastMonthEnd,
            LP:this.toPercent(element1.loosPercent),
            custPercent:this.toPercent(element.custPercent) 
          });
        });
        name.push(element.dims4);
        monthDatLine.push(element.monthDatLine);
        yearDatLine.push(element.yearDatLine);
        incrThanYearBegin.push(element.incrThanYearBegin);
        incrThanLastMonthEnd.push(element.incrThanLastMonthEnd);
      });
      // console.log(name);
      // console.log(xData);
      // console.log(yData);
      // console.log(monthDatLine);
      // console.log(yearDatLine);

      function setSeries() {
        var _series = [];
        for (let i = 0; i < name.length; i++) {
          _series.push({
            name: name[i],
            type: "line",
            smooth: true,
            markLine: {
              //最大值和最小值
              data: [
                {
                  name: "阈值54",
                  yAxis: monthDatLine[i],
                  silent: true,
                  symbolSize: 5,
                  label: {
                    show: "true",
                    position: "end",
                    formatter: "月初私行基准线"
                  },
                  lineStyle: {
                    normal: {
                      width: 1,
                      color: myColor[i]
                    }
                  }
                },
                {
                  name: "阈值5",
                  yAxis: yearDatLine[i],
                  symbolSize: 5,
                  label: {
                    show: "true",
                    position: "end",
                    formatter: "年初私行基准线"
                  },
                  lineStyle: {
                    normal: {
                      width: 1,
                      color: myColor[i]
                    }
                  }
                }
              ]
            },
            lineStyle: {
              normal: {
                width: 1
              }
            },
            symbol: "circle",
            symbolSize: 4,
            itemStyle: {
              normal: {
                color: myColor[i]
                // borderColor: 'blue', //拐点边框颜色
              }
            },
            data: yData[i]
          });
        }
        return _series;
      }
      // 绘制图表
      let option = {
        title: {
          text: "近一年客户趋势",
          top: "3%",
          left: "30px"
        },
        // tooltip: {
        //   trigger: "axis",
        //   axisPointer: {
        //     lineStyle: {
        //       color: "#57617B"
        //     }
        //   }
        // },
        tooltip: {
          trigger: "item",
          padding: 10,
          // formatter: "{a} <br/>{b} : {c}%"
          formatter: function(params) {
            console.log(params);
            let data = params.data;
            if (params.componentType === "markLine") {
              return;
            } else {
              let str = `
              ${data.XD}${params.seriesName} : ${data.value} </br>
              占全客户比 : ${data.custPercent}</br>
              较上月末净增 : ${data.ME} </br>
              较年初净增 : ${data.YB} </br>
              ${data.XD}${params.seriesName} </br>
              流失率 : ${data.LP}
            `;
              return str;
            }
          }
        },
        legend: {
          // icon: 'diamond',
          itemGap: 13,
          data: ["非0基客", "基客", "优客", "贵宾", "私行"],
          selected: {
            基客: false,
            优客: false,
            贵宾: false,
            私行: false
          },

          top: "3%",
          left: "right",
          textStyle: {
            fontSize: 12,
            color: "#000"
          }
        },
        grid: {
          bottom: "10%",
          right: "15%"
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#57617B"
              }
            },
            data: xData[0]
          }
        ],
        yAxis: [
          {
            type: "value",
            axisTick: {
              show: false
            },
            axisLine: {
              lineStyle: {
                color: "#57617B"
              }
            },
            axisLabel: {
              margin: 10,
              textStyle: {
                color: "rgba(0,0,0,.6)",
                fontSize: "12"
              }
            },
            splitLine: {
              lineStyle: {
                color: "rgba(255,255,255,0.1)"
              }
            }
          }
        ],
        // visualMap: [{
        //     show: false,
        //     pieces: [
        //         {
        //             gt: 0,
        //             lt: 30,
        //             color: '#FFFF00',
        //         },
        //         {
        //             gt: 30,
        //             lt: 54,
        //             color: '#00A1EA',
        //         }
        //     ],
        //     outOfRange: {
        //         color: '#FF5D1D',
        //     },
        // }, ],
        series: setSeries()
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      YDate: state => {
        return state.OneYearCustTrend;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    YDate: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-nian", this.YDate);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-nian", this.YDate);
    });
  }
};
</script>
<style>
#echarts-nian {
  width: 100%;
  height: 100%;
}
</style>