<template>
  <div class="home-wrapper">
    <HomeHeader class="home-header" @sendData="getData"></HomeHeader>
    <HomeContent class="home-content"></HomeContent>
  </div>
</template>
<script>
const qs = require("qs");
import HomeHeader from "./HomeHeader";
import HomeContent from "./HomeContent";
import { mapState } from "vuex";
export default {
  name: "Home",
  components: {
    HomeHeader,
    HomeContent
  },
  methods: {
    getQueryVariable(variable) {
      var query = window.location.search.substring(1);
      var vars = query.split("&");
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    },
    getData(params) {
      let self = this;
      // "?userID=110099999&userOrg=9800"
      if (!params.orgId) {
        var userOrg = self.getQueryVariable("userOrg");
        params.orgId = userOrg;
      }
      // 磁贴信息
      self.$http
        .post("getTopBaseData", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_TopBaseData", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 获取年龄性别风险等级信
      self.$http
        .post("getBaseInfoData", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_BaseInfoData", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 分行地图
      self.$http
        .post("getbranMapInfo", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_branMapInfo", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 30天客户趋势
      self.$http
        .post("getOneMonthCustTrend", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_OneMonthCustTrend", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 一年客户趋势
      self.$http
        .post("getOneYearCustTrend", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_OneYearCustTrend", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 客户分层
      self.$http
        .post("getCustomerSlice", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_CustomerSlice", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });

      // 卡分层 客户资产分布
      self.$http
        .post("getCardSlice", qs.stringify(params))
        .then(function(response) {
          console.log(response);
          self.$store.commit("Set_CardSlice", response.data);
        })
        .catch(function(error) {
          console.log(error);
        });
      if (params.orgId !== "9900") {
        // 支行地图
        self.$http
          .post("getbranMapInfo", qs.stringify(params))
          .then(function(response) {
            console.log(response);
            self.$store.commit("Set_MapAllData", response.data);
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        // 总行地图
        self.$http
          .post("getHeadMapInfo", qs.stringify(params))
          .then(function(response) {
            console.log(response);
            self.$store.commit("Set_MapAllData", response.data);
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    }
