<template>
  <div id="echarts-xingbie">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      // var value = pieData;
      var m2R2Data = [];
      pieData.forEach(element => {
        m2R2Data.push({
          value: element.amt,
          name: element.dims4
        });
      });

      // var m2R2Data = [
      //   {
      //     value: 635,
      //     name: "男",
      //   },
      //   {
      //     value: 310,
      //     name: "女",
      //   }
      // ];

      // 绘制图表
      let option = {
        color: [
          "rgb(81,85,208)",
          "rgb(183,221,234)",
          "#FE5656",
          "#01E17E",
          "#3DD1F9",
          "#FFAD05"
        ],
        tooltip: {
          trigger: "item",
          formatter: function(parms) {
            var str =
              parms.seriesName +
              "</br>" +
              parms.marker +
              "" +
              parms.data.name +
              "</br>" +
              "数量：" +
              parms.data.value +
              "</br>" +
              "占比：" +
              parms.percent +
              "%";
            return str;
          }
        },

        series: [
          {
            name: "性别",
            type: "pie",
            center: ["50%", "45%"],
            radius: ["0%", "55%"],
            clockwise: false, //饼图的扇区是否是顺时针排布
            avoidLabelOverlap: false,
            label: {
              normal: {
                formatter: "{per|{b}:{d}%} ",
                align: "center",
                borderWidth: 0,
                borderRadius: 4,
                padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#000",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#000",
                  fontSize: 13
                }
              }
            },
            labelLine: {
              normal: {
                length: 10,
                length2: 50
                // smooth: true
              }
            },
            data: m2R2Data
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      sexDate: state => {
        return state.BaseInfoData.sex;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    sexDate: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-xingbie", this.sexDate);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-xingbie", this.sexDate);
    });
  }
};
</script>
<style>
#echarts-xingbie {
  width: 100%;
  height: 100%;
}
</style>