<template>
  <div id="echarts-kehufenceng">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
        toPercent(val, flag) {
      //数组转小数
      if (flag) {
        return (Math.round(val * 10000) / 100).toFixed(2);
      } else {
        return (Math.round(val * 10000) / 100).toFixed(2) + "%";
      }
    },
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      var value = [];
      let _this = this;
      pieData.forEach(element => {
        value.push({
          value: element.amt,
          name: element.dims4,
          branPercent:element.branPercent,
          headPercent:element.headPercent
        });
      });
      // value = [
      //   { value: 60, name: "优客" },
      //   { value: 40, name: "贵宾" },
      //   { value: 20, name: "私行" },
      //   { value: 80, name: "基客" },
      //   { value: 100, name: "非0基客" },
      //   { value: 120, name: "0资产" }
      // ];
      // 绘制图表
      let option = {
        backgroundColor: "#ffffff",
        color: [
          "rgb(242,84,135)",
          "rgb(255,140,77)",
          "rgb(251,213,80)",
          "rgb(78,204,203)",
          "rgb(79,160,255)",
          "rgb(81,85,208)"
        ],
        tooltip: {
          trigger: "item",
          // formatter: "{a} <br/>{b} : {c}%"
          formatter: function (params) {
            let data = params.data
            let str = `
            ${data.name}数: ${data.value} </br>
            占总客户 </br>
            ${_this.toPercent(data.branPercent)} </br>
             ${
              data.headPercent
                ? "全行" +
                  data.name +
                  "</br> 占比" +
                  _this.toPercent(data.headPercent)
                : ""
            }
            `
            return str
          }

        },

        series: [
          {
            name: "预期",
            type: "funnel",
            left: "20%",
            width: "60%",
            height: "60%",
            sort: "ascending",
            label: {
              normal: {
                position: "inside",
                formatter: "{b}"
              }
              // emphasis: {
              //     position:'inside',
              //     formatter: '{b}预期: {c}%'
              // }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            itemStyle: {
              normal: {
                opacity: 1
              }
            },
            data: value
          }
          // {
          //     name: '实际',
          //     type: 'funnel',
          //     left: '10%',
          //     width: '80%',
          //     maxSize: '80%',
          //     sort: 'ascending',
          //     label: {
          //         normal: {
          //             position: 'inside',
          //             formatter: '{c}%',
          //             textStyle: {
          //                 color: '#777'
          //             }
          //         },
          //         emphasis: {
          //             position:'inside',
          //             formatter: '{b}实际: {c}%'
          //         }
          //     },
          //     itemStyle: {
          //         normal: {
          //             opacity: 0.4,
          //             borderColor: '#fff',
          //             borderWidth: 2
          //         }
          //     },
          //     data: [
          //         {value: 30, name: '访问'},
          //         {value: 10, name: '咨询'},
          //         {value: 5, name: '订单'},
          //         {value: 50, name: '点击'},
          //         {value: 80, name: '展现'}
          //     ]
          // }
        ]
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      CustomerSlice: state => {
        return state.CustomerSlice;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    CustomerSlice: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-kehufenceng", this.CustomerSlice);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-kehufenceng", this.CustomerSlice);
    });
  }
};
</script>
<style>
#echarts-kehufenceng {
  width: 100%;
  height: 100%;
}
</style>