<template>
  <div id="echarts-fengxian">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      var value = [];
      pieData.forEach(element => {
        value.push({
          value:element.amt,
          name: element.dims4
        });
      });

      // value = [
      //         {
      //           value: 10,
      //           name: "R1"
      //         },
      //         {
      //           value: 5,
      //           name: "R2"
      //         },
      //         {
      //           value: 15,
      //           name: "R3"
      //         },
      //         {
      //           value: 25,
      //           name: "R4"
      //         },
      //         {
      //           value: 20,
      //           name: "R5"
      //         }
      //       ]

      // 绘制图表
      let option = {
        // backgroundColor:"#0B1837",
        color: [
          "#EAEA26",
          "#906BF9",
          "#FE5656",
          "#01E17E",
          "#3DD1F9",
          "#FFAD05"
        ],
        grid: {
          left: 0,
          top: 0,
          bottom: 0,
          right: 0,
          containLabel: true
        },
        tooltip: {
          trigger: "item",
          formatter: "{b} : {c} ({d}%)"
        },

        series: [
          {
            stack: "a",
            type: "pie",
            radius: ["20%", "80%"],
            roseType: "area",
            zlevel: 10,
            // label: {
            //     normal: {
            //         show: true,
            //         formatter: "{c}",
            //         textStyle: {
            //             fontSize: 12,
            //         },
            //         position: 'outside'
            //     },
            //     emphasis: {
            //         show: true
            //     }
            // },
            label: {
              normal: {
                formatter: "{per|{b}} ",
                align: "center",
                position: "inside", //标签的位置
                borderWidth: 0,
                borderRadius: 4,
                // padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#fff",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#fff",
                  fontSize: 13
                }
              }
            },
            labelLine: {
              normal: {
                show: true,
                length: 10,
                length2: 15
              },
              emphasis: {
                show: false
              }
            },
            data: value
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      rankDate: state => {
        return state.BaseInfoData.rank;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    rankDate: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-fengxian", this.rankDate);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-fengxian", this.rankDate);
    });
  }
};
</script>
<style>
#echarts-fengxian {
  width: 100%;
  height: 100%;
}
</style>