<template>
  <div id="echarts-nianling">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));

      let myChart = this.$echarts.init(document.getElementById(id));
      // var value = pieData;

      var m2R2Data = [];
      pieData.forEach(element => {
        m2R2Data.push({
          name: element.dims4,
          value: element.amt
        });
        return m2R2Data;
      });
      // var m2R2Data = [
      //   {
      //     value: 635,
      //     name: "1-20岁"
      //     // itemStyle: { color: "rgb(150,203,255)" }
      //   },
      //   {
      //     value: 310,
      //     name: "20-30岁"
      //     // itemStyle: { color: "rgb(81,85,208)" }
      //   },
      //   {
      //     value: 635,
      //     name: "30-40岁"
      //     // itemStyle: { color: "rgb(70,160,255)" }
      //   },
      //   {
      //     value: 310,
      //     name: "40-50岁"
      //     // itemStyle: { color: "rgb(78,204,203)" }
      //   },
      //   {
      //     value: 635,
      //     name: "50-60岁"
      //     // itemStyle: { color: "rgb(89,203,116)" }
      //   },
      //   {
      //     value: 310,
      //     name: "60岁+"
      //     // itemStyle: { color: "rgb(251,213,80)" }
      //   },
      //   {
      //     value: 635,
      //     name: "50-60岁"
      //     // itemStyle: { color: "rgb(242,84,135)" }
      //   },
      //   {
      //     value: 310,
      //     name: "60岁+"
      //     // itemStyle: { color: "rgb(170,235,119)" }
      //   }
      // ];

      // 绘制图表
      let option = {
        color: [
          "rgb(150,203,255)",
          "rgb(81,85,208)",
          "rgb(70,160,255)",
          "rgb(78,204,203)",
          "rgb(89,203,116)",
          "rgb(251,213,80)",
          "rgb(242,84,135)",
          "rgb(170,235,119)"
        ],
        tooltip: {
          trigger: "item",
          formatter: function(parms) {
            var str =
              parms.seriesName +
              "</br>" +
              parms.marker +
              "" +
              parms.data.name +
              "</br>" +
              "数量：" +
              parms.data.value +
              "</br>" +
              "占比：" +
              parms.percent +
              "%";
            return str;
          }
        },

        series: [
          {
            name: "性别",
            type: "pie",
            center: ["50%", "45%"],
            radius: ["30%", "55%"],
            clockwise: false, //饼图的扇区是否是顺时针排布
            avoidLabelOverlap: false,
            label: {
              normal: {
                formatter: "{per|{b}} ",
                align: "center",
                borderWidth: 0,
                borderRadius: 4,
                padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#000",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#000",
                  fontSize: 13
                }
              }
            },
            labelLine: {
              normal: {
                length: 10,
                length2: 50
                // smooth: true
              }
            },
            data: m2R2Data
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      nianLing: state => {
        return state.BaseInfoData.age;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    nianLing: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-nianling", this.nianLing);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-nianling", this.nianLing);
    });
  }
};
</script>
<style>
#echarts-nianling {
  width: 100%;
  height: 100%;
}
</style>