<template>
  <div class="homeheader-wrapper">
    <div class="homeheader-wrapper-left">
      <el-select
        class="margin-5 homeheader-select"
        v-model="value"
        placeholder="请选择"
        size="mini"
        @change="bindSelectChange"
      >
        <el-option
          class="homeheader-select-option"
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <div class="margin-5 wrapper-switch" @click="bindSwitch">
        <div :class="['left-switch' , valueswitch? 'active-switch':''] ">按月</div>

        <div :class="['right-switch',valueswitch?'':'active-switch']">按日</div>
      </div>

      <el-date-picker
        class="homeheader-el-date-picker"
        v-model="mounthDefaultShow"
        size="mini"
        type="month"
        placeholder="选择月"
        v-if="valueswitch"
        @change="bindMounthPicker"
      ></el-date-picker>
      <el-date-picker
        class="homeheader-el-date-picker"
        type="date"
        size="mini"
        v-model="timeDefaultShow"
        placeholder="选择日期"
        v-if="!valueswitch"
        @change="bindTimePicker"
      ></el-date-picker>
    </div>

    <div class="homeheader-title">零售客户全景视图</div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  name: "HomeHeader",
  data() {
    return {
      options: [
        {
          value: "0",
          label: "存量客户"
        },
        {
          value: "1",
          label: "当年新增客户"
        }
      ],
      value: "0",
      valueswitch: false,
      mounthDefaultShow: "", // 默认月份
      timeDefaultShow: "" // 默认日期
    };
  },
  methods: {
    sendHomeData() {
      this.$emit("sendData", {
        customerType: this.value,
        byDayOrMonth: !this.valueswitch ? "01" : "02",
        dateString: !this.valueswitch
          ? this.timeDefaultShow
          : this.mounthDefaultShow,
      });
    },
    bindSwitch() {
      this.valueswitch = !this.valueswitch;
      this.sendHomeData();
    },
    bindSelectChange(e) {
      console.log(e);
      this.sendHomeData();
    },
    bindMounthPicker(e) {
      this.mounthDefaultShow = e.format("yyyy-MM-dd");
      this.sendHomeData();
    },
    bindTimePicker(e) {
      this.timeDefaultShow = e.format("yyyy-MM-dd");
      this.sendHomeData();
    }
  },
  watch: {
    value(p) {
      console.log(p);
      this.$store.commit("set_value", p);
    },
    valueswitch(p) {
      console.log(p);
      this.$store.commit("set_valueswitch", p);
    },
    mounthDefaultShow(p) {
      console.log(p);
      this.$store.commit("set_mounthDefaultShow", p);
    },
    timeDefaultShow(p) {
      console.log(p);
      this.$store.commit("set_timeDefaultShow", p);
    }
  },
  mounted() {
    let that = this;
    let date = new Date();
    date.setDate(date.getDate() - 1);
    that.timeDefaultShow = date.format("yyyy-MM-dd");
    that.mounthDefaultShow = date.getFullYear() + "-" + date.getMonth();
    that.sendHomeData();
  }
};
</script>

<style scoped>
.homeheader-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0 20px;
  color: #ffffff;
  background-color: rgb(17, 65, 183);
}
.homeheader-title {
  position: absolute;
  top: 0px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  line-height: 74px;
  width: 20%;
  height: 100%;

  font-size: 30px;
  font-weight: 600;
  /* border: 1px solid lawngreen; */
}
.homeheader-wrapper-left {
  display: flex;
  width: 50%;
  height: 100%;
  align-items: center;
  /* border: 1px solid red; */
}
.homeheader-switch {
  color: #ffffff !important;
}
/* .lajielement-wrapper {
  display: flex;
} */
.margin-5 {
  margin: 0 5px;
}
.homeheader-select {
  width: 160px;
}
.homeheader-el-date-picker {
  width: 160px;
}

.wrapper-switch {
  box-sizing: border-box;
  display: flex;
  width: 100px;
  height: 25px;
  border-radius: 25px;
  cursor: pointer;
  border: 1px solid #ffffff;
}
.left-switch {
  box-sizing: border-box;
  width: 100%;
  height: 25px;
  text-align: center;
  line-height: 25px;
  border-radius: 25px;
}
.right-switch {
  box-sizing: border-box;
  width: 100%;
  height: 25px;
  text-align: center;
  line-height: 25px;
  border-radius: 25px;
}
.active-switch {
  left: 0px;
  color: rgb(17, 65, 183);
  background-color: #ffffff;
}
</style>