<template>
  <div id="echarts-kehuzichan">echart1</div>
</template>

<script>
import echarts from "echarts";
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    toPercent(val, flag) {
      //数组转小数
      if (flag) {
        return (Math.round(val * 10000) / 100).toFixed(2);
      } else {
        return (Math.round(val * 10000) / 100).toFixed(2) + "%";
      }
    },
    drawLine(id, pieData) {
      // 基于准备好的dom，初始化echarts实例
      if (document.getElementById(id) == null) {
        return;
      }
      echarts.dispose(document.getElementById(id));
      let _this = this;

      let myChart = this.$echarts.init(document.getElementById(id));
      var value = pieData;
      var innerValue = [
        {
          value: value.value,
          name: value.name
        }
      ];
      // 绘制图表
      let option = {
        // backgroundColor:"#0B1837",
        color: [
          "#EAEA26",
          "#906BF9",
          "#FE5656",
          "#01E17E",
          "#3DD1F9",
          "#FFAD05"
        ],
        grid: {
          left: 0,
          top: 0,
          bottom: 0,
          right: 0,
          containLabel: true
        },
        tooltip: {
          trigger: "item",
          // formatter: "{b} : {c} ({d}%)"
          formatter: function(params) {
            let data = params.data;
            return `
            ${data.name} : ${data.value}  </br> 占${
              _this.KeHuZiChanData.name
            } </br> ${_this.toPercent(data.FenHangValue) == "NaN%" ? "100%" :_this.toPercent(data.FenHangValue)}
            </br> ${
              data.QuanHangValue
                ? "全行" +
                  _this.KeHuZiChanData.name +
                  data.name +
                  "</br> 占比" +
                  _this.toPercent(data.QuanHangValue)
                : ""
            }
            `;
          }
        },

        series: [
          {
            stack: "a",
            type: "pie",
            radius: ["40%", "60%"],
            zlevel: 10,
            label: {
              normal: {
                formatter: "{per|{b}} ",
                align: "center",
                position: "inside", //标签的位置
                borderWidth: 0,
                borderRadius: 4,
                // padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#fff",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#fff",
                  fontSize: 13
                }
              }
            },
            itemStyle: {
              color: "pink",
              borderColor: "#FFF",
              normal: {
                // borderWidth: "5",
                borderColor: "#FFF"
              }
            },
            labelLine: {
              normal: {
                show: true,
                length: 10,
                length2: 15
              },
              emphasis: {
                show: false
              }
            },
            data: value.KeHuZiChanData
          },
          {
            type: "pie",
            radius: ["0", "20%"],
            zlevel: 10,
            label: {
              normal: {
                formatter: "{per|{b}} ",
                align: "center",
                position: "inside", //标签的位置
                borderWidth: 0,
                borderRadius: 4,
                // padding: [0, -60, 10],
                height: 20,
                rich: {
                  per: {
                    color: "#fff",
                    fontSize: 13,
                    // padding: [5, 8],
                    borderRadius: 2
                  }
                },
                textStyle: {
                  color: "#fff",
                  fontSize: 13
                }
              }
            },
            itemStyle: {
              color: "pink",
              borderColor: "#FFF",
              normal: {
                borderWidth: "0",
                borderColor: "#FFF"
              }
            },
            labelLine: {
              normal: {
                show: true,
                length: 10,
                length2: 15
              },
              emphasis: {
                show: false
              }
            },
            data: innerValue
          }
        ]
      };

      myChart.setOption(option);
    }
  },
  computed: {
    ...mapState({
      KeHuZiChanData: state => {
        return state.KeHuZiChanData;
      }
    })
  },
  watch: {
    //监听echart Pie数据变化
    KeHuZiChanData: {
      handler() {
        let self = this;
        this.$nextTick(function() {
          self.drawLine("echarts-kehuzichan", this.KeHuZiChanData);
        });
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    let self = this;
    self.$nextTick(function() {
      self.drawLine("echarts-kehuzichan", this.KeHuZiChanData);
    });
  }
};
</script>
<style>
#echarts-kehuzichan {
  width: 100%;
  height: 100%;
}
</style>